.about {
    margin-bottom: 10rem;
    @include breakpoint(mobile) {
        margin-bottom: 5rem;
    }
}

.about__row {
    display: flex;

    @include breakpoint(mobile) {
        flex-direction: column;
    }
}

.about__left-col {
    width: 31rem;
    margin-right: 3rem;
    flex-shrink: 0;

    @include breakpoint(mobile) {
        width: 100%;
        margin: 0;
        flex-shrink: 0;

        order: 3;
    }
}

.about__right-col {
    width: 50%;
    flex-grow: 1;
    @include breakpoint(mobile) {
        width: 100%;
        flex-grow: 0;
        margin-bottom: 3.5rem;
    }
}

.about__guarantee {
    position: relative;
    z-index: 1;
    background: #e3f7eb;
    padding: 1.2rem;
    width: 100%;
    max-width: 26rem;

    @include breakpoint(mobile) {
        max-width: none;
    }
}

.about__guarantee-heading {
    font-weight: 500;
    font-size: 2rem;
    line-height: 2.3rem;
    margin-bottom: 0.8rem;
}

.about__guarantee-text {
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 2rem;
    letter-spacing: 0.02em;
    margin-bottom: 4rem;

    @include breakpoint(mobile) {
        max-width: 26rem;
    }
}

.about__guarantee-image {
    position: absolute;
    width: 8rem;
    height: 8rem;
    object-fit: contain;
    right: 2rem;
    bottom: 1rem;
    z-index: -1;
    pointer-events: none;
}

.about__navigation {
    @include breakpointMF(small-tablet) {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 5rem;
        margin-top: -2rem;
    }

    @include breakpoint(mobile) {
        margin-bottom: 3rem;
    }
}

.about__navigation-link {
    @include breakpointMF(small-tablet) {
        padding: 2rem 2.2rem;
        font-weight: 500;
        font-size: 1.4rem;
        line-height: 1.6rem;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        position: relative;
        font-family: var(--franklin-c);
        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            width: 100%;
            bottom: 0;
            height: 0.2rem;
            background-color: var(--primary-color);
            transition: opacity 0.2s;
            opacity: 0;
        }

        .no-touch &:hover,
        .touch &:active {
            color: var(--primary-color);
        }

        &.active {
            color: var(--primary-color);

            &::after {
                opacity: 1;
            }
        }
    }

    @include breakpoint(mobile) {
        margin: 0 calc(-1 * var(--content-padding));
        border-bottom: 1px solid #ebebeb;
        padding: 1.2rem var(--content-padding);
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2rem;
        display: block;

        &:last-child {
            margin-bottom: 0;
        }

        .no-touch &:hover,
        .touch &:active {
            color: var(--primary-color);
        }
        &.active {
            color: var(--primary-color);
        }
    }
}

.about__text-content {
    margin-bottom: 3.5rem;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 2rem;
    letter-spacing: 0.02em;
    p {
        margin-bottom: 2rem;
        &:last-child {
            margin-bottom: 0;
        }
    }

    img {
        display: block;
        width: auto;
        height: auto;
        margin: 3rem 0;
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.about__main-products {
    margin-bottom: 3.5rem;
}

.about__main-products-heading {
    font-weight: 500;
    font-size: 2.8rem;
    line-height: 1.2;
    margin-bottom: 3rem;

    @include breakpoint(mobile) {
        margin-bottom: 2rem;
        font-size: 2.2rem;
        line-height: 1.2;
    }
}

.about__main-products-list {
    display: flex;
    flex-direction: column;
}

.about__main-products-list-item {
    margin-bottom: 1rem;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 2rem;
    letter-spacing: 0.02em;
    &:last-child {
        margin-bottom: 0;
    }
}

.about__main-products-link {
    border-bottom: 1px solid currentColor;
    transition: color 0.2s, border-color 0.2s;

    .no-touch &:hover,
    .touch &:active {
        border-color: transparent;
    }
}

.about__ideology-heading {
    font-weight: 500;
    font-size: 2.8rem;
    line-height: 1.2;
    margin-bottom: 3rem;
    @include breakpoint(mobile) {
        margin-bottom: 2rem;
        font-size: 2.2rem;
        line-height: 1.2;
    }
}

.about__ideology-list-item {
    display: flex;
    align-items: flex-start;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 2rem;
    letter-spacing: 0.02em;
    margin-bottom: 1rem;
    &:last-child {
        margin-bottom: 0;
    }
    &::before {
        content: counter(ordered-list);
        width: 3.2rem;
        height: 3.2rem;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        background: #e3f7eb;
        border-radius: 50%;
        font-weight: bold;
        font-size: 1.8rem;
        line-height: 1.2;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: #03a448;
        margin-right: 2.5rem;
        flex-shrink: 0;
    }
}

.about__ideology-text {
    padding-top: 0.6rem;
}

.about__content-wrapper {
    width: 100%;
    max-width: 79rem;
}
